import React from "react";
import "./create.scss";
import { Menu } from "../../components/menu/Menu";
import { Navbar } from "../../components/navbar/Navbar";
import { useForm } from "react-hook-form";
import { createPost } from "../../redux/create/action";
import { useDispatch } from "react-redux";
import { getPost } from "../../redux/post/action";
import { useParams } from "react-router-dom";
import { useTypesSelector } from "../../hooks/useTypeSelector";

export const Create: React.FC = () => {
  const dispatch = useDispatch();

  const onSubmitHandlear = (data: any) => {
    dispatch(createPost(data));
    window.location.reload();
  };

  const {
    register,
    formState: { errors },
    handleSubmit,
  } = useForm({
    mode: "onBlur",
  });

  return (
    <>
      <Menu></Menu>
      <Navbar></Navbar>
      <div className="create">
        <form onSubmit={handleSubmit(onSubmitHandlear)}>
          <div className="create__headerContainer">
            <textarea
              {...register("title", {
                required: " !",
                minLength: {
                  value: 6,
                  message: "   6 !",
                },
                maxLength: {
                  value: 35,
                  message: "     ",
                },
              })}
              placeholder=" ..."
              className="create__headerInput"
            ></textarea>
            <div className="create__context">
              {errors?.title && <p>{errors?.title?.message || "Error!"}</p>}
            </div>
          </div>
          <div className="create__descriptionContainer">
            <div className="create__header"> </div>
            <textarea
              {...register("description", {
                required: " !",
                minLength: {
                  value: 6,
                  message: "   6 !",
                },
                maxLength: {
                  value: 160,
                  message: "     ",
                },
              })}
              className="create__input"
            ></textarea>
            <div className="create__context">
              {errors?.description && (
                <p>{errors?.description?.message || "Error!"}</p>
              )}
            </div>
          </div>
          <div className="create__linkContainer">
            <div className="create__header">  :</div>
            <div className="create__form">
              <div className="create__container">
                <textarea
                  {...register("photoUrl", {
                    required: " !",
                    minLength: {
                      value: 6,
                      message: "   6 !",
                    },
                    maxLength: {
                      value: 100,
                      message:
                        "     ",
                    },
                  })}
                  placeholder=" URL..."
                  className="create__input"
                ></textarea>
                <button disabled className="create__button">
                  
                </button>
              </div>
              <div className="create__context">
                {errors?.photoUrl && (
                  <p>{errors?.photoUrl?.message || "Error!"}</p>
                )}
              </div>
            </div>
          </div>
          <div className="create__textContainer">
            <div className="create__header"> </div>
            <textarea
              {...register("text", {
                required: " !",
                minLength: {
                  value: 6,
                  message: "   6 !",
                },
              })}
              className="create__input"
            ></textarea>
            <div className="create__context">
              {errors?.text && <p>{errors?.text?.message || "Error!"}</p>}
            </div>
          </div>
          <button type="submit" className="create__button">
            
          </button>
        </form>
      </div>
    </>
  );
};
